import React from 'react';
import {AddTodoForm} from './AddTodoForm';
import {useSelector} from "../../lib";
import {SingleTodo} from './SingleTodo';
import {
    AppBar,
    createStyles, Grid,
    List,
    Tabs,
    makeStyles,
    Theme,
    Tab, useTheme, Box
} from '@material-ui/core';
import {SortTodoButton} from "./SortTodoButton";
import SwipeableViews from 'react-swipeable-views';

function a11yProps(index: number) {
    return {
        id: `full-width-tab-${index}`,
        'aria-controls': `full-width-tabpanel-${index}`,
    };
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            width: '100%',
            backgroundColor: theme.palette.background.paper,
        },
        nested: {
            paddingLeft: theme.spacing(4),
        },
    }),
);

export const TodoListPage = () => {

    const classes = useStyles();
    const theme = useTheme();
    const [value, setValue] = React.useState(0);

    const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
        setValue(newValue);
    };

    const handleChangeIndex = (index: number) => {
        setValue(index);
    };

    const todos = useSelector(state => state.todo.todos)

    return <div>
        <AddTodoForm/>
        <Grid
            container
            justify="center"
            alignItems="center"
        >

            <div className={classes.root}>
                <AppBar position="static" color="default">
                    <Tabs
                        value={value}
                        onChange={handleChange}
                        indicatorColor="primary"
                        textColor="primary"
                        variant="fullWidth"
                    >
                        <Tab label="Текущие" {...a11yProps(0)} />
                        <Tab label="Завершенные" {...a11yProps(1)} />
                    </Tabs>
                </AppBar>

                <SwipeableViews
                    axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                    index={value}
                    onChangeIndex={handleChangeIndex}
                >
                        <div
                            hidden={value !== 0}
                            id={`full-width-tabpanel-${0}`}
                            aria-labelledby={`full-width-tab-${0}`}
                        >
                            {value === 0 && (
                                <Box p={3}>
                                    <Grid item xs={12}>
                                        <List
                                            component="div"
                                            aria-labelledby="nested-list-subheader"
                                            subheader={
                                                <Grid container alignItems='center'>
                                                    <Grid item xs={1}>
                                                        <SortTodoButton name={'важность'} sortType={'importance'}/>
                                                    </Grid>
                                                    <Grid item xs={10}/>
                                                    <Grid item xs={1}>
                                                        <SortTodoButton name={'изменен'} sortType={'date'}/>
                                                    </Grid>
                                                </Grid>
                                            }
                                            className={classes.root}
                                        >
                                            {
                                                todos.map((todo, index) => {
                                                    return <SingleTodo key={index} todo={todo}/>
                                                })
                                            }
                                        </List>
                                    </Grid>
                                </Box>
                            )}
                        </div>

                </SwipeableViews>
            </div>


            <Grid item xs={2}/>
        </Grid>
        <hr/>
    </div>

}