import React from 'react';
import {TTodo} from "../../types";
import {Collapse, Grid, List, ListItem, ListItemIcon, ListItemText} from "@material-ui/core";
import Brightness1Icon from '@material-ui/icons/Brightness1';
import {ExpandLess, ExpandMore} from "@material-ui/icons";
import CheckCircleOutlineIcon from '@material-ui/icons/CheckCircleOutline';
import CreateIcon from '@material-ui/icons/Create';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import {closeTodoSubMenu, finishTodo, openEditMode, openTodoSubMenu, removeTodo} from "../../lib/actions";
import {useDispatch} from "react-redux";
import {AddTodoForm} from "./AddTodoForm";


export const SingleTodo: React.FC<{ todo: TTodo }> = ({todo}) => {

    const dispatch = useDispatch();
    const time = new Date(todo.lastEditDate);

    let importanceColor = '';
    switch (todo.importance) {
        case '1':
            importanceColor = 'red';
            break;
        case '2':
            importanceColor = 'green';
            break;
        case '3':
            importanceColor = 'grey';
            break;
    }

    return <div>
        <ListItem>
            {!todo.isEditMode
                ? <ListItem
                    button
                    onClick={() => todo.isOpenSubMenu
                        ? dispatch(closeTodoSubMenu(todo.id))
                        : dispatch(openTodoSubMenu(todo.id))
                    }>
                    <ListItemIcon>
                        <Brightness1Icon style={{color: importanceColor}}/>
                    </ListItemIcon>
                    <ListItemText primary={todo.text}/>
                    <div className={'todo-list-item__date'}>
                        <span>
                            {`${time.getDay()}/${time.getMonth()}/${time.getFullYear()}`}
                        </span>
                        <span>
                            {`${time.getHours()}:${time.getMinutes()}`}
                        </span>
                    </div>
                    {todo.isOpenSubMenu
                        ? <ExpandLess/>
                        : <ExpandMore/>
                    }
                </ListItem>
                : <Grid item xs={12}><AddTodoForm todo={todo}/></Grid>
            }
        </ListItem>

        <Collapse in={todo.isOpenSubMenu} timeout="auto" unmountOnExit>
            <List component="div" disablePadding>
                <ListItem>

                    <ListItem button className={'todo-list-item'}>
                        <CheckCircleOutlineIcon
                            onClick={()=>dispatch(finishTodo(todo))}
                        />
                    </ListItem>
                    <ListItem
                        button
                        className={'todo-list-item'}
                        onClick={() => {
                            dispatch(closeTodoSubMenu(todo.id))
                            dispatch(openEditMode(todo.id))
                        }}>
                        <CreateIcon/>
                    </ListItem>
                    <ListItem
                        button
                        className={'todo-list-item'}
                        onClick={() => dispatch(removeTodo(todo.id))}
                    >
                        <DeleteForeverIcon/>
                    </ListItem>

                </ListItem>
            </List>
        </Collapse>

    </div>
}