import React, {useState} from 'react';
import {useDispatch} from "react-redux";
import {addTodo, closeEditMode, editTodo} from "../../lib/actions";
import {Form} from "react-final-form";
import CloseIcon from '@material-ui/icons/Close';
import SaveIcon from '@material-ui/icons/Save';
import {
    Button,
    FormControl,
    FormControlLabel,
    Grid, IconButton,
    Radio,
    RadioGroup, Snackbar,
    TextField
} from "@material-ui/core";
import {TTodo} from "../../types";

export const AddTodoForm: React.FC<{ todo?: TTodo }> = ({todo}) => {

    const [radioValue, setRadioValue] = useState('1');
    const [openError, setOpenError] = useState(false);
    const [formText, setFormText] = useState(todo ? todo.text : '')
    const dispatch = useDispatch()

    const addSubmit = () => {
        if (formText.length >= 1) {
            dispatch(addTodo(formText, radioValue))
            setFormText('');
            setRadioValue('1');
        } else {
            setOpenError(true)
        }
    }

    const editSubmit = () => {
        if (todo) dispatch(editTodo(todo.id, formText, radioValue))
    }

    const closeError = (event: React.SyntheticEvent | React.MouseEvent, reason?: string) => {
        if (reason === 'clickaway') {
            return;
        }
        setOpenError(false);
    };


    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        setRadioValue((event.target as HTMLInputElement).value);
    };

    return <div>
        <Form onSubmit={todo ? editSubmit : addSubmit}>
            {props => (
                <form onSubmit={props.handleSubmit}>
                    <Grid
                        container
                        direction="row"
                        justify="center"
                        alignItems="center"
                    >
                        <Grid item xs={todo ? 11 : 10}>
                            <TextField
                                name="text"
                                fullWidth
                                variant="outlined"
                                multiline={true}
                                value={formText}
                                onChange={(e) => setFormText(e.target.value)}
                                label={todo ? "Редактировать" : "Добавить в список"}
                            />
                        </Grid>{todo && <Grid item xs={1}>
                        <Button type='submit' ><SaveIcon/></Button>
                        <Button onClick={()=> dispatch(closeEditMode(todo.id))}><CloseIcon/></Button>
                    </Grid>}

                        {!todo && <Grid item xs={2}>
                            <Button
                                style={{width: '100%'}}
                                type="submit"
                                color="primary"
                                size="small"
                                className={''}
                            >Добавить</Button>
                        </Grid>}
                        <Grid item xs={12}>
                            <Grid
                                container
                                direction="row"
                                justify="flex-end"
                                alignItems="center"
                            >
                                <Grid item xs={12}>
                                    <FormControl component="fieldset">
                                        <RadioGroup row={true} name="importance" value={radioValue}
                                                    onChange={handleChange}>
                                            <FormControlLabel value={'1'} control={<Radio style={{color: 'red'}}/>}
                                                              label={"Важно"}/>
                                            <FormControlLabel value={'2'} control={<Radio style={{color: 'green'}}/>}
                                                              label={"Средне"}/>
                                            <FormControlLabel value={'3'} control={<Radio style={{color: 'grey'}}/>}
                                                              label={"Неважно"}/>
                                        </RadioGroup>
                                    </FormControl>
                                </Grid>

                            </Grid>
                        </Grid>
                    </Grid>

                </form>
            )}
        </Form>

        <Snackbar
            anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
            }}
            open={openError}
            autoHideDuration={3000}
            onClose={closeError}
            message="Поле не может быть пустым"
            action={
                <React.Fragment>
                    <Button color="secondary" size="small" onClick={() => setOpenError(false)}>
                        Закрыть
                    </Button>
                    <IconButton size="small" aria-label="close" color="inherit" onClick={() => setOpenError(false)}>
                        <CloseIcon fontSize="small"/>
                    </IconButton>
                </React.Fragment>
            }
        />

    </div>
}