import * as actionTypes from './actionTypes';
import {Reducer} from "redux";
import {TTodosState, TTodoActions} from '../types'

const initialState: TTodosState = {
    todos: [],
    finishedTodos: []
}

const todoReducer: Reducer<TTodosState, TTodoActions> = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.ADD_TODO:
            return {
                ...state,
                todos: [...state.todos,
                    {
                        id: state.todos[0] ? state.todos[state.todos.length - 1].id + 1 : 0,
                        text: action.text,
                        importance: action.importance,
                        isOpenSubMenu: false,
                        isEditMode: false,
                        lastEditDate: Date.now(),
                    },
                ]
            }
        case actionTypes.REMOVE_TODO:
            return {
                ...state,
                todos: state.todos.filter(t => t.id !== action.id)
            }
        case actionTypes.OPEN_TODO_SUB_MENU:
            return {
                ...state,
                todos: state.todos.map(t => {
                    if (t.id === action.id) {
                        return {
                            ...t,
                            isOpenSubMenu: true
                        }
                    } else {
                        return {
                            ...t,
                            isOpenSubMenu: false,
                            isEditMode: false
                        }
                    }
                })
            }
        case actionTypes.CLOSE_TODO_SUB_MENU:
            return {
                ...state,
                todos: state.todos.map(t => {
                    if (t.id === action.id) {
                        return {
                            ...t,
                            isOpenSubMenu: false,
                            isEditMode: false
                        }
                    } else {
                        return t
                    }
                })
            }
        case actionTypes.OPEN_TODO_EDIT_MODE:
            return {
                ...state,
                todos: state.todos.map(t => {
                    if (t.id === action.id) {
                        return {
                            ...t,
                            isEditMode: true
                        }
                    } else {
                        return {
                            ...t,
                            isEditMode: false
                        }
                    }
                })
            }
        case actionTypes.CLOSE_TODO_EDIT_MODE:
            return {
                ...state,
                todos: state.todos.map(t => {
                    if (t.id === action.id) {
                        return {
                            ...t,
                            isEditMode: false
                        }
                    } else {
                        return t
                    }
                })
            }
        case actionTypes.EDIT_TODO:
            return {
                ...state,
                todos: state.todos.map(t => {
                    if (t.id === action.id) {
                        return {
                            ...t,
                            text: action.text,
                            importance: action.importance,
                            isEditMode: false,
                            lastEditDate: Date.now()
                        }
                    } else {
                        return t
                    }
                })
            }
        case actionTypes.SORT_TODO_BY:
            if (action.sortType === 'importance') {
                let sortedTodos = [...state.todos.sort((a, b) => a.importance > b.importance ? 1 : -1)];
                return {...state, todos: action.reverse ? sortedTodos.reverse() : sortedTodos}
            }
            if (action.sortType === 'date') {
                let sortedTodos = [...state.todos.sort((a, b) => a.lastEditDate > b.lastEditDate ? 1 : -1)];
                return {...state, todos: action.reverse ? sortedTodos.reverse() : sortedTodos}
            }
            return state
        case actionTypes.FINISH_TODO:
                return {
                    ...state,
                    todos: state.todos.filter(todo => todo.id !== action.todo.id),
                    finishedTodos: [...state.finishedTodos, action.todo]
                }
        default:
            return state;
    }
}

export default todoReducer;