import * as actionTypes from './actionTypes'
import {TTodo} from "../types";

export const addTodo = (text: string, importance: string) => ({type: actionTypes.ADD_TODO,text,importance})
export const removeTodo = (id: number) => ({type: actionTypes.REMOVE_TODO, id})
export const openTodoSubMenu = (id:number) => ({type: actionTypes.OPEN_TODO_SUB_MENU, id})
export const closeTodoSubMenu = (id:number) => ({type: actionTypes.CLOSE_TODO_SUB_MENU, id})
export const openEditMode = (id:number) => ({type: actionTypes.OPEN_TODO_EDIT_MODE, id})
export const closeEditMode = (id:number) => ({type: actionTypes.CLOSE_TODO_EDIT_MODE, id})
export const editTodo = (id:number, text: string, importance: string) => ({type: actionTypes.EDIT_TODO, id, text, importance})
export const sortTodoBy = (sortType: string, reverse: boolean) => ({type: actionTypes.SORT_TODO_BY, sortType, reverse})
export const finishTodo = (todo: TTodo) => ({type: actionTypes.SORT_TODO_BY, todo});
